@import "./variables";

body {
  overflow: hidden;
  margin: 0;
  font-family: "Avenir", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: $background-color-dark;
  color: $text-color-light;
}

#root {
  height: 100vh;
  overflow-y: scroll;
  overflow-x: hidden;
  scroll-snap-type: y mandatory;
  scroll-behavior: smooth;
}

section {
  height: 100vh;
  scroll-snap-align: start;
  position: relative;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

h1,
h2,
h3,
h4,
p,
ul,
li {
  margin: 0px;
  padding: 0px;
}

h1 {
  font-size: 36px;
  font-family: "Arvo", serif;
  font-weight: 300;
  margin-bottom: 16px;
}

h2 {
  font-size: 28px;
  font-weight: 100;
  margin: 24px 0px 16px;
}

h3 {
  font-size: 22px;
  font-weight: 400;
  margin: 16px 0px 8px;
}

h4 {
  font-weight: normal;
  font-size: 18px;
}

li,
p {
  font-size: 14px;
}

ul {
  list-style-type: none;
}

/* Buttons */

.no-style-button {
  font: inherit;
  color: inherit;
  background: inherit;
  border: none;
  outline: none;
  cursor: text;
}

.link-button {
  cursor: pointer;
  padding: 4px 16px;
  border: 1px solid $text-color-light;
  background: none;
  color: $text-color-light;
  font: inherit;
}

.link-button:focus {
  outline: 0;
}

.link-button:hover {
  background-color: $background-color-light-hover;
}

.link-button-narrow {
  padding: 4px 8px;
}

.link-button-dark {
  color: $text-color-dark;
  border: 2px solid $text-color-dark;
}

.link-button-dark:hover {
  background-color: $background-color-light-hover;
}

.icon-button {
  display: flex;
  align-items: center;

  h3 {
    margin: 4px 8px;
    font-weight: 300;
  }
}

.icon-button-space {
  justify-content: space-between;
}

.clickable-text-element {
  color: $element-color-light;
}

.clickable-text-element:hover {
  cursor: pointer;
  color: $element-color-light-hover;
}

.clickable-text-element-dark {
  color: $element-color-dark;
}

.clickable-text-element-dark:hover {
  color: $element-color-dark-hover;
  cursor: pointer;
}

.clickable-background-element {
  background-color: $background-color-dark-cover-opaque;
}

.clickable-background-element:hover {
  cursor: pointer;
  background-color: $background-color-dark-cover-opaque-hover;
}

.disabled-button {
  cursor: text;
}
.disabled-button:hover {
  background: none;
}

/* Elements */

.horizontal-line {
  border-top: 1px solid $text-color-light;
  margin: 16px 0px;
  width: 100%;
}

.vertical-line {
  position: absolute;
  width: 1px;
  height: 0px;
  border-left: 1px solid;
}

.hide-element {
  display: none !important;
}
